
import { Component, Vue } from 'vue-property-decorator';

@Component({
    components: {}
})
export default class TemplateCnp extends Vue {
    itemList = [
        {
            label: 'my-demo-blog',
            desc: '我用demo写博客',
            path: '/my-demo-blog'
        },
        {
            label: 'my-notesbook',
            desc: '笔记',
            path: '/notesbook'
        },
        {
            label: 'Comming Soon',
            desc: '敬请期待',
            path: ''
        }
    ];
    clicked = false;
    index = -1;

    goRouter(path: string, index: any) {
        let that = this;
        that.clicked = true;
        that.index = index;
        setTimeout(()=>{
            that.$router.push(path);
        }, 200)
    }
}
